<template>
	<view>
		<button class="gobackBTN" @click="goback">
			<uni-icons type="arrowleft" size="24"></uni-icons>
		</button>
		<swiper style="height: 250px;" :indicator-dots="indicatorDots">
			<swiper-item>
				<view class="swiper-item">
					<image :src="room.pictures[0].image_file"></image>
				</view>
			</swiper-item>
			<swiper-item>
				<view class="swiper-item">
					<image :src="room.pictures[1].image_file"></image>
				</view>
			</swiper-item>
			<swiper-item>
				<view class="swiper-item">
					<image :src="room.pictures[2].image_file"></image>
				</view>
			</swiper-item>
		</swiper>
		<view class="main-background">
			<view class="city-content">
				<!-- <view class="border-shadow" style="width: 160rpx; height: 60rpx; border-radius: 30rpx;"> -->
				<button class="border-shadow sharebtn" type="default" open-type="share">
					<uni-icons type="redo" size="20" color="#f07373"></uni-icons>
				</button>
				<view class="border-shadow favbtn dis-flex">
					<uni-fav></uni-fav>
					<text style="margin-left: 10rpx;">22</text>
				</view>
				<!-- 修改日期开始 -->
				<view class="date-change mt-1">
					<uni-calendar ref="calendar" :insert="false" @confirm="confirm"></uni-calendar>
					<text class="ml-1">11.23</text>
					<text>—</text>
					<text>11.24</text>
					<view style=" display: inline-block;">
						<text class="ml-1">共</text>
						<text>1</text>
						<text>晚</text>
					</view>
					<button class="right main-color mr-1" style="background: none; font-size: 30rpx; margin-top: 17rpx;" @click="open">修改日期</button>
				</view>
				<!-- 修改日期结束
				<!-- 房源文字介绍开始 -->
				<view>
					<view class="describe mt-1">
						<view>
							<view class="banner-title mb-05 mr-1">{{room.title}}曼谷高级公寓&泳池&bts </view>
							<view style="font-size: 24rpx;">{{ room.areaage }}平米 {{ room.number_of_living_room }}个卧室 {{ room.number_of_beds}}张床
								{{ room.number_of_living_room}}个客厅
								<view>最多住{{ room.maximum_number_of_people }}人</view>
							</view>
						</view>
						<view class="comment-content" style="text-align: center;
						font-size: 28rpx; color: #5451BD;">
							<view style="margin-top: 15rpx;">
								<view>5.0</view>
								<view>⭐⭐⭐⭐⭐️</view>
								<view>——</view>
								<view>25条评论</view>
							</view>
						</view>
					</view>
					<view class="describe-tag">
						<view>如实描述</view>
						<view>干净整洁</view>
						<view>入住顺利</view>
						<view>沟通顺畅</view>
						<view>位置便利</view>
						<view>位置便利</view>
					</view>
				</view>
				<!-- 房源文字介绍结束 -->

				<!-- 配套设施开始 -->
				<view>
					<view class="mb-title">配套设施</view>

					<uni-collapse style="background-color: #F8F8F8;">
						<view style="display: flex; justify-content: space-around; background-color: #F8F8F8;">

							<view style="display: flex; flex-direction: column;" v-if="room.washing_machine === true">
								<uni-icons class="iconlist" type="help"></uni-icons>
								<text style="font-size: 20rpx; text-align: center;">洗衣机</text>
							</view>

							<view style="display: flex; flex-direction: column;" v-if="room.iron === true">
								<uni-icons class="iconlist" type="help"></uni-icons>
								<text style="font-size: 20rpx; text-align: center;">熨斗</text>
							</view>

							<view style="display: flex; flex-direction: column;" v-if="room.hair_dryer=== true">
								<uni-icons class="iconlist" type="help"></uni-icons>
								<text style="font-size: 20rpx; text-align: center;">吹风机</text>
							</view>

							<view style="display: flex; flex-direction: column;" v-if="room.wifi === true">
								<uni-icons class="iconlist" type="help"></uni-icons>
								<text style="font-size: 20rpx; text-align: center;">wifi</text>
							</view>

						</view>
						<uni-collapse-item title="全部设施">

							<view style="display: flex; justify-content: space-around; flex-wrap: wrap;">

								<view style="display: flex; flex-direction: column; width: 110rpx;" v-if="room.refrigerator === true">
									<uni-icons class="iconlist" type="help"></uni-icons>
									<text style="font-size: 20rpx; text-align: center;">冰箱</text>
								</view>

								<view style="display: flex; flex-direction: column; width: 110rpx;" v-if="room.television === true">
									<uni-icons class="iconlist" type="help"></uni-icons>
									<text style="font-size: 20rpx; text-align: center;">电视机</text>
								</view>

								<view style="display: flex; flex-direction: column; width: 110rpx;" v-if="room.kettle === true">
									<uni-icons class="iconlist" type="help"></uni-icons>
									<text style="font-size: 20rpx; text-align: center;">电热水壶</text>
								</view>
								<view style="display: flex; flex-direction: column; width: 110rpx;" v-if="room.microwave === true">
									<uni-icons class="iconlist" type="help"></uni-icons>
									<text style="font-size: 20rpx; text-align: center; ">微波炉</text>
								</view>
							</view>
							<view style="display: flex;">

								<view style="display: flex; flex-direction: column; width: 110rpx; margin-left: 28rpx" v-if="room.kitchen_ware === true">
									<uni-icons class="iconlist" type="help"></uni-icons>
									<text style="font-size: 20rpx; text-align: center;">厨具</text>
								</view>

							</view>

						</uni-collapse-item>
					</uni-collapse>
				</view>

				<!-- 配套设施结束 -->

				<!-- 房客点评开始 -->
				<view class="mb-title">房客点评</view>
				<view>

					<view>
						<view style="background-color: #E1E0FF; 
						height: 100rpx; line-height: 100rpx;font-size: 30rpx;">
							<text class="ml-5">5.0</text>
							<text class="ml-5">⭐️⭐️⭐️⭐️⭐️</text>
							<text class="ml-1">（1条评论）</text>
						</view>
					</view>

					<view class="mt-05">
						<!-- 用户头像 -->
						<view class="dis-flex">
							<uni-icons type="person-filled" size="32"></uni-icons>
							<view class="dis-flex-co" style="font-size: 25rpx;">
								<text>lipari</text>
								<text>2020.11.30</text>
							</view>
							<!-- 评分 -->
							<view style="margin-left: 260rpx;">
									<text class="iconfont icon-ziyuan391" ></text>
									⭐⭐⭐⭐⭐
								️</view>
						</view>

						<!-- 评论内容 -->
						<view style="font-size: 25rpx;margin-top: 10rpx;">
							{{room.comments[0].content}}
						</view>

						<view style="text-align: center; font-size: 30rpx; color: #5451BD;
						" @click='goAllComment()'>
							<view class="mt-05">
								<text>全部评价</text>
								<uni-icons class="right" color="#5451BD" size="18" type="arrowright" />
							</view>
						</view>
					</view>


				</view>
				<!-- 房客点评结束 -->

				<!-- 房源位置开始 -->
				<view class="mb-title">房源位置</view>
				<view class="dis-flex" style="justify-content: space-around;" @click="goMap()">
					<uni-icons type="map-pin-ellipse" style="line-height: 80rpx;"></uni-icons>
					<text style="font-size: 28rpx;width: 460rpx;">{{room.address}}
					</text>
					<uni-icons type="arrowright" style="line-height: 80rpx;"></uni-icons>
				</view>
				<!-- 房源位置结束 -->

				<view class="mt-1">
					<uni-collapse style="background-color: #F8F8F8;">
						<view style="display: flex; justify-content: space-around; background-color: #F8F8F8;">

							<view style="display: flex; flex-direction: column;" v-if="room.ATM === true">
								<uni-icons class="iconlist" type="help"></uni-icons>
								<text style="font-size: 20rpx; text-align: center;">ATM</text>
							</view>

							<view style="display: flex; flex-direction: column;" v-if="room.part === true">
								<uni-icons class="iconlist" type="help"></uni-icons>
								<text style="font-size: 20rpx; text-align: center;">停车场</text>
							</view>

							<view style="display: flex; flex-direction: column;" v-if="room.post_office == true">
								<uni-icons class="iconlist" type="help"></uni-icons>
								<text style="font-size: 20rpx; text-align: center;">邮局</text>
							</view>

							<view style="display: flex; flex-direction: column;" v-if="room.convenience_store == true">
								<uni-icons class="iconlist" type="help"></uni-icons>
								<text style="font-size: 20rpx; text-align: center;">便利店</text>
							</view>

						</view>
						<uni-collapse-item title="全部设施">

							<view style="display: flex;">

								<view style="display: flex; flex-direction: column; margin-left: 28rpx" v-if="room.supermarket === true">
									<uni-icons class="iconlist" type="help"></uni-icons>
									<text style="font-size: 20rpx; text-align: center;">超市</text>
								</view>

							</view>

						</uni-collapse-item>
					</uni-collapse>
				</view>
				<!-- 预定须知开始 -->
				<view class="mb-title">预定须知</view>
				<view class="dis-flex mb-1">
					<text style="margin-right: 45rpx; font-size: 28rpx; font-weight: bold;">房屋守则</text>
					<view class="dis-flex-co" style="font-size: 28rpx;">
						<text>适合儿童入住（2-12岁）</text>
						<text>适合儿童入住（2-12岁）</text>
					</view>
				</view>

				<view class="dis-flex mb-1">
					<text class="mr-1 " style="font-size: 28rpx; font-weight: bold;">入住/退房</text>
					<view class="dis-flex-co" style="font-size: 28rpx;">
						<text>16:00后入住，11:00前退房</text>
					</view>
				</view>

				<view class="dis-flex mb-1">
					<text class="mr-1 " style="font-size: 28rpx; font-weight: bold;">入住/退房</text>
					<view class="dis-flex-co" style="font-size: 28rpx;">
						<text>16:00后入住，11:00前退房</text>
					</view>
				</view>

				<view class="dis-flex mb-1">
					<text style="margin-right: 45rpx ; font-size: 28rpx; font-weight: bold;">取消说明</text>
					<view class="dis-flex-co" style="font-size: 28rpx;">
						<text>预定确定过后48小时内免费取消</text>
						<text class="mr-05" style="color: #5451BD;" @click="goCancelPolicy()">查看更多</text>
					</view>
				</view>

				<view class="dis-flex mb-1" v-if="room.special_information !== '' ">
					<text style="margin-right: 45rpx ; font-size: 28rpx; font-weight: bold;">其他特殊需求</text>
					<view class="dis-flex-co" style="font-size: 28rpx;">
						<text>{{room.special_information}}</text>
					</view>
				</view>


				<view style="height: 180rpx;"></view>

				<!-- 预定须知结束 -->
			</view>
		</view>
		<uni-goods-nav :fill="true" :options="options" :buttonGroup="buttonGroup" @at-root @click="onClick" @buttonClick="buttonClick" />
	</view>
</template>

<script>
	import _ from '../../underscore.js'
	import TestData from '../../store/test_data.js'
	export default {
		data() {
			return {
				room: '',
				indicatorDots: true,
				options: [{
					icon: 'headphones',
					text: '客服'
				}],
				buttonGroup: [{
					text: '立即预定',
					backgroundColor: '#5451bd',
					color: '#fff'
				}]
			}
		},
		onLoad(event) {
			this.roomdata = TestData.initRoomDataForHomePage();
			// this.room = _.filter(TestData.initRoomDataForHomePage().data,function(room){
			// 	return room.id == event.id
			// })
			this.roomdata.some(element => {
				// console.log(element)
				if (element.id == event.id) {
					this.room = element;
				}
			})
			console.log(this.room)

			//详情标题
			// uni.setNavigationBarTitle({
			// 	title: this.banner.title
			// });
		},
		methods: {
			onClick(e) {
				uni.navigateTo({
					url: "../taking/taking"
				})
			},
			buttonClick(e) {
				// console.log(e)
				// this.options[2].info++
				uni.navigateTo({
					url: "../OrderSubmit/OrderSubmit"
				})
			},
			goback() {
				uni.navigateBack()
			},
			open() {
				this.$refs.calendar.open();
			},
			goAllComment() {
				uni.navigateTo({
					url: "../comment/comment"
				})
			},
			goMap() {
				uni.navigateTo({
					url: "../map/map"
				})
			},
			goCancelPolicy() {
				uni.navigateTo({
					url: "../CancelPolicy/CancelPolicy"
				})
			},
		}
	}
</script>

<style lang="scss">
	.main-background {
		margin-top: -54rpx;
		background: #FFFFFF;
		height: 900rpx;
	}

	.city-content {
		margin: 10rpx auto;
	}

	.gobackBTN {
		position: fixed;
		top: 0;
		z-index: 999;
		background: none;
	}

	.date-change {
		height: 50px;
		line-height: 50px;
		background-color: #F6F5F9;
		border-radius: 15px
	}

	.date-change text {
		font-size: 28rpx;
	}

	.describe {
		display: flex;
	}

	.comment-content {
		width: 200rpx;
		height: 200rpx;
		background-color: #F8F8F8;
		border-radius: 15rpx;
	}

	.describe-tag {
		display: flex;
		font-size: 24rpx;
		;
		margin: 15rpx 0;
		justify-content: space-between;
	}

	.describe-tag view {
		background-color: #DCDCDC
	}

	.iconlist {
		font-size: 50rpx !important;
		background-color: #FFFFFF;
		border-radius: 15rpx;
		text-align: center;
		width: 100rpx;
		height: 100rpx;
		line-height: 100rpx;
		margin-top: 10rpx;
	}

	.sharebtn {
		width: 100rpx;
		height: 100rpx;
		border-radius: 100rpx;
		background: #ffffff;
		margin: -56rpx 0 0 548rpx;
	}

	.favbtn {
		width: 120rpx;
		height: 100rpx;
		border-radius: 100rpx;
		margin: -100rpx 0 0 390rpx;
		line-height: 50px;
	}
</style>
